import {ITodo} from "./todo";
import * as T from './actions/types';

export interface IAppState {
  todos: ITodo[];
  lastUpdate: Date;
}

export const INITIAL_STATE: IAppState = {
  todos: [],
  lastUpdate: null
};

export function rootReducer(state, action) {
  switch (action.type) {
    case T.ADD_TODO:
      action.todo.id = state.todos.length + 1;
      return Object.assign({}, state, {
        todos: state.todos.concat(Object.assign({}, action.todo)),
        lastUpdate: new Date()
      });
    case  T.TOOGLE_TODO:
      let todo = state.todos.find(t => t.id === action.id);
      let index = state.todos.indexOf(todo);
      return Object.assign({}, state, {
        todos: [
          ...state.todos.slice(0, index),
          Object.assign({}, todo, {isComplited: !todo.isComplited}),
          ...state.todos.slice(index + 1)],
        lastUpdate: new Date()
      });

    case T.REMOVE_TODO:
      console.log("My id is ->"+action.toString());
      console.log(action.id);
      todo = state.todos.find(t => t.id === action.id);
      console.log("Im removing the todo -> "+todo);
      index = state.todos.indexOf(todo);
      console.log("Index -> "+index);
      return Object.assign({}, state, {
        todos: state.todos.filter(t => t.id !== action.id),
        lastUpdate: new Date()
      });

    case  T.REMOVE_ALL_TODOS:
      return Object.assign({}, state, {
        todos: [],
        lastUpdate: new Date()
      });
  }
  return state;
}




